import { openDB } from 'idb';
import { useState, useEffect } from 'react';
import initDB from './InitDB';

// Hook pour grer la base de donnes IndexedDB
export const useIndexedDB = (_DB_name) => {
    const [DB_name, setDB_name] = useState(_DB_name)
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true)
    const [drawer, setDrawer] = useState(false)


    // Fonction pour ajouter une donne
    const addData = async (_data) => {
        setIsLoading(true)
        const db = await initDB();
        const tx = db.transaction(DB_name, 'readwrite');
        const store = tx.objectStore(DB_name);
        await store.add(_data);
        await tx.done;
        loadData(); // Recharge les donnes aprs ajout
    };

    // Fonction pour modifier une donne
    const updateData = async (id, _data) => {
        setIsLoading(true)
        const db = await initDB();
        const tx = db.transaction(DB_name, 'readwrite');
        const store = tx.objectStore(DB_name);
        await store.put({ ..._data, id });
        await tx.done;
        loadData(); // Recharge les donnes aprs modification
    };

    // Fonction pour supprimer une donne
    const deleteData = async (id) => {
        setIsLoading(true)
        const db = await initDB();
        const tx = db.transaction(DB_name, 'readwrite');
        const store = tx.objectStore(DB_name);
        await store.delete(id);
        await tx.done;
        loadData(); // Recharge les donnes aprs suppression
    };

    const clearTable = async () => {
        const db = await initDB(); // Assure-toi d'avoir une mthode pour initialiser la DB
        const tx = db.transaction(DB_name, 'readwrite'); // Remplace 'people' par le nom de ton store
        const store = tx.objectStore(DB_name); // Remplace galement ici
        await store.clear(); // Vide la table
        await tx.done;
        loadData()
        console.log("Table vide avec succs !");
      };
      

    // Fonction pour charger toutes les donnes
    const loadData = async () => {
        const db = await initDB();
        const tx = db.transaction(DB_name, 'readonly');
        const store = tx.objectStore(DB_name);
        const allData = await store.getAll();
        setData(allData); // Met  jour l'tat avec les donnes rcupres
        setIsLoading(false)
    };

    useEffect(()=>{
        if (!drawer) {
            setIsLoading(true)
            loadData()
        }
    }, [drawer])

    // Charge les donnes au montage du composant
    useEffect(() => {
        loadData();
    }, []);
    return {isLoading, data,drawer, setDrawer, addData, updateData, deleteData, loadData, clearTable };
};
