import React, { useEffect, useState } from 'react';
import { Button, Drawer, Space, Spin, Table, Tag, Modal } from 'antd';
import { useIndexedDB } from '../Models/useIndexDB';
import { DataBuilding, DataFoncier, DataPlanting } from '../Models/InitDB';
import { columnBuilding, columnFoncier, columnPlanting } from './tableColumn';
import BuildingForm from '../Forms/BuildingForm';
import FoncierForm from '../Forms/FoncierForm';
import PlantingForm from '../Forms/PlantingForm';


// Fonction pour convertir les donnes en CSV
const exportToCSV = (data, filename = 'data.csv') => {
  // Vrifie que les donnes ne sont pas vides
  if (!data || !data.length) {
    return;
  }

  // Rcupre les cls des objets comme en-ttes du fichier CSV
  const headers = Object.keys(data[0]).join(',');

  // Construit les lignes du CSV avec les valeurs
  const csvData = data.map(row => {
    return Object.values(row).map(value => `"${value}"`).join(',');
  });

  // Ajoute les en-ttes au dbut du CSV
  const csvContent = [headers, ...csvData].join('\n');

  // Cre un lien de tlchargement et l'excute
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  const url = URL.createObjectURL(blob);
  link.setAttribute('href', url);
  link.setAttribute('download', filename);
  link.style.visibility = 'hidden';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};



export default function PAPTable({ DBname }) {
  const { data, isLoading, drawer, setDrawer, clearTable } = useIndexedDB(DBname)
  const [columns, setColumns] = useState([])
  const [pageSize, setPageSize] = useState(3); // Valeur par dfaut pour tlphone

  const [drawerTitle, setDrawerTitle] = useState(null)
  const [idObject, setIdObject] = useState(null)
  const [object, setObject] = useState(null)

  const [open, setOpen] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [modalText, setModalText] = useState('Vous allez supprimer les donenr de la base de donne pour'.concat(DBname));
  const showModal = () => {
    setOpen(true);
  };

  const handleOk = async () => {
    setModalText('suppresion des donnes en cours ...');
    await clearTable();
    setConfirmLoading(true);
    setTimeout(() => {
      setOpen(false);
      setConfirmLoading(false);
    }, 3000);
  };

  const handleCancel = () => {
    setOpen(false);
  };

  const handleExport = () => {
    exportToCSV(data, 'data_'.concat(DBname, '.csv'));
  };

  columnBuilding[1] = {
    title: 'PROJET',
    dataIndex: 'project',
    key: 'project',
    render: (text, record) => (
      <div style={{ whiteSpace: 'nowrap' }} onClick={() => {
        openDrawer(record)
      }}>
        {text.toUpperCase()}
      </div>
    ),
  }

  columnFoncier[1] = {
    title: 'PROJET',
    dataIndex: 'project',
    key: 'project',
    render: (text, record) => (
      <div style={{ whiteSpace: 'nowrap' }} onClick={() => {
        openDrawer(record)
      }}>
        {text.toUpperCase()}
      </div>
    ),
  }

  columnPlanting[1] = {
    title: 'PROJET',
    dataIndex: 'project',
    key: 'project',
    render: (text, record) => (
      <div style={{ whiteSpace: 'nowrap' }} onClick={() => {
        openDrawer(record)
      }}>
        {text.toUpperCase()}
      </div>
    ),
  }

  const openDrawer = (object = null) => {
    setDrawer(true)
    setObject(object)
  }

  const closeDrawer = () => {
    setDrawer(false)
    setObject(null)
  }

  useEffect(() => {
    if (DBname === DataBuilding) {
      setDrawerTitle("Donne pour les batiments")
      setColumns(columnBuilding)
    } else if (DBname === DataFoncier) {
      setDrawerTitle("Donne pour le foncier")
      setColumns(columnFoncier)
    } else {
      setDrawerTitle("Donne pour les plantations")
      setColumns(columnPlanting)
    }
  }, [DBname])


  useEffect(() => {
    const handleResize = () => {
      const width = window.innerWidth;
      if (width < 768) {
        setPageSize(10); // Taille pour tlphone
      } else {
        setPageSize(20); // Taille pour tablette et plus grand
      }
    };
    handleResize(); // Vrifie la taille au chargement
    window.addEventListener('resize', handleResize); // coute les changements de taille de fentre
    return () => {
      window.removeEventListener('resize', handleResize); // Nettoie l'couteur lors du dmontage du composant
    };
  }, []);


  return (
    <React.Fragment>
      {isLoading ?
        <div style={{ height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <Spin />
        </div> :
        <React.Fragment>
          <Table
            columns={columns}
            dataSource={data}
            pagination={{ pageSize }}
            scroll={{ x: 1300 }}
          />
          <Space wrap style={{padding: '1em'}}>
            <Button size="large" type='primary' onClick={() => openDrawer()} style={{ backgroundColor: '#0000ff' }}> Ajouter donne</Button>
            <Button size='large' type="primary" onClick={handleExport} style={{ backgroundColor: '#00ff00' }}>Exporter en CSV</Button>
            <Button size='large' type="dash" onClick={showModal} style={{ backgroundColor: '#ff0000' }}>
              Supprimer les donnes
            </Button>
          </Space>
          <Modal
            title="Suppression de donne"
            open={open}
            onOk={handleOk}
            confirmLoading={confirmLoading}
            onCancel={handleCancel}
          >
            <p>{modalText}</p>
          </Modal>
        </React.Fragment>
      }
      <Drawer
        onClose={closeDrawer}
        closable
        open={drawer}
        title={drawerTitle}
      >
        {object ?
          <React.Fragment>
            {DBname === DataBuilding ? <BuildingForm initialValue={object} /> : DBname === DataFoncier ? <FoncierForm initialValue={object} /> : DBname === DataPlanting ? <PlantingForm initialValue={object} /> : "pas de formualaire"}
          </React.Fragment>
          :
          <React.Fragment>
            {DBname === DataBuilding ? <BuildingForm /> : DBname === DataFoncier ? <FoncierForm /> : DBname === DataPlanting ? <PlantingForm /> : "pas de formualaire"}
          </React.Fragment>
        }
      </Drawer>

    </React.Fragment>
  );
}
